<template>
	<nav>
		<ul>
			<li v-for='nav in getNav | filterNav' v-link="{name:nav.rname}">{{nav.name}}</li>
		</ul>
	</nav>
</template>
<script>
 import { getNav} from '../vuex/getters';
  export default {
    vuex: {
      getters: {
        getNav: getNav,
        getUser:({userMes})=>userMes.user,
      },
    },
    router:{
    	canReuse:false
    }
  };
</script>
<style scoped>
	nav{margin: 0 auto;text-align: center;background-color: #444}
	nav ul{overflow: hidden;display: inline-block;padding:0;margin:5px 0;margin-top:0;}
	nav ul li{float:left;list-style:none;color:#ccc;padding:0 10px;font-size:1rem;font-weight: bold}
</style>


